import React from 'react';
import ReactDOM from 'react-dom';
import store from 'store';
import './index.css';
import Ticket from './ticket_katatatakiken_naname.png';
import {TOPNUMBERS, SIDENUMBERS, CLEAR} from './numbers.js';

const FILLED = 'ISFILLED';
const CROSSED = 'ISCROSSED';

function Square(props) {
    let className = 'square';
    let value = null;
    if (props.value === FILLED) {
        className += ' isFilled';
    } else if (props.value === CROSSED) {
        className += ' isCrossed';
    } else {
        value = props.value;
    }

    return (
        <button className={className} onClick={props.onClick}>
            {value}
        </button>
    );
}

class Board extends React.Component {
    renderSquare(i) {
        return (
            <Square
                value={this.props.squares[i]}
                onClick={() => this.props.onClick(i)}
                key={"s-" + i}
            />
        );
    }

    renderNumber(i, idx) {
        return (
            <Square
                value={i}
                key={"s-" + idx}
            >
            </Square>
        );
    }

    renderBox(num) {
        let res = [];
        for (var i = 0; i < 5; i++) {
            let inner = [];
            for (var j = 0; j < 5; j++) {
                inner.push(this.renderSquare(num));
                num += 1;
            }
            res.push(<li key={i}>{inner}</li>);
        }
        return (
        <div className="board-box">
            {res}
        </div>
        );
    }

    renderSide(num) {
        let res = [];
        for (var i = 0; i < 5; i++) {
            let inner = [];
            let nums = SIDENUMBERS[num + i];
            for (var j = 0; j < 6; j++) {
                inner.push(this.renderNumber(nums[j], j));
            }
            res.push(<li key={i} >{inner}</li>)
        }
        return (
            <div className="board-box side">
                {res}
            </div>
        );
    }

    renderTop(num) {
        let res = [];
        for (var i = 0; i < 5; i++) {
            let inner = [];
            let nums = TOPNUMBERS[num + i];
            for (var j = 0; j < 6; j++) {
                inner.push(<li key={j}>{this.renderNumber(nums[j], j)}</li>);
            }
            res.push(<div className="number-top" key={i} >{inner}</div>)
        }
        return (
            <div className="board-box">
                {res}
            </div>
        );
    }

    renderCorner() {
        let res = [];
        for (var i = 0; i < 6; i++) {
            let inner = [];
            for (var j = 0; j < 6; j++) {
                inner.push(<li key={j}><Square key={j}></Square></li>);
            }
            res.push(<div className="number-top" key={i} >{inner}</div>)
        }
        return (
            <div className="board-box">
                {res}
            </div>
        );
    }

    render() {
        return (
            <div>
                <div className="top">
                    {this.renderCorner()}
                    {this.renderTop(0)}
                    {this.renderTop(5)}
                    {this.renderTop(10)}
                </div>
                <div className="under">
                    <div className="number-side">
                        {this.renderSide(0)}
                        {this.renderSide(5)}
                        {this.renderSide(10)}
                    </div>
                    <div className="board">
                        <div>
                            {this.renderBox(0)}
                            {this.renderBox(25)}
                            {this.renderBox(50)}
                        </div>
                        <div>
                            {this.renderBox(75)}
                            {this.renderBox(100)}
                            {this.renderBox(125)}
                        </div>
                        <div>
                            {this.renderBox(150)}
                            {this.renderBox(175)}
                            {this.renderBox(200)}
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

class Game extends React.Component {
    constructor(props) {
        super(props);
        const start = store.get('user') ? store.get('user').squares : Array(15 * 15).fill(null);
        this.state = {
            squares: start,
        };
        this.values = [null, FILLED, CROSSED];
    }

    handleClick(i) {
        const squares = this.state.squares.slice();
        const valueIdx = this.values.indexOf(squares[i]);

        if (calculateWinner(squares)) {
            return;
        }

        squares[i] = (valueIdx + 1) < this.values.length ? this.values[valueIdx + 1] : this.values[0];

        store.set('user', {squares: squares});

        this.setState({
            squares: squares,
        });
    }
    render() {
        let message;
        if (calculateWinner(this.state.squares)) {
            message = (
                        <div className="message">
                            <p>
                                CLEAR!!!<br></br>
                                <br></br>
                                いつもありがとう
                            </p>
                            <img src={Ticket} className="ticket"></img>
                        </div>
            );
        } else {
            message = (
                        <div className="message">
                            <p>
                                パズルは自動的に
                                <br></br>
                                保存されるよ
                                <br></br>
                                <br></br>
                                リセットする時は
                                <br></br>
                                上のボタンを押してね
                            </p>
                        </div>
            );
        }

        return (
            <div>
                <h1>Mother's day PUZZLE</h1>
                <div className="game">
                    <div className="game-board">
                        <Board
                            squares={this.state.squares}
                            onClick={i => this.handleClick(i)}
                        />
                    </div>
                    <div className="game-info">
                        <button
                            className="reset-button"
                            onClick={() => {
                                store.remove();
                                this.setState({
                                    squares: Array(15 * 15).fill(null),
                                });
                            }}>
                            最初から
                        </button>
                        {message}
                    </div>
                </div>
            </div>
        );
    }
}

// ========================================

ReactDOM.render(
    <Game />,
    document.getElementById('root')
);

function calculateWinner(squares) {
    for (let i = 0; i < CLEAR.length; i++) {
        let square_state = squares[i] === FILLED ? 1 : 0;
        console.log(CLEAR[i],square_state,i);
        if (CLEAR[i] != square_state) {
            console.log('not clear');
            return null;
        }
    }
    console.log('clear');
    return squares;
}
